.contents {
    width: 100%;
    height: auto;
    display: block;
    padding: 5rem;
}

.btns {
    border-radius: 10px;
    box-shadow: 0 1px 2px -1px rgb(0 0 0 / 2%), 0 2px 4px -2px rgb(0 0 0 / 8%), 0 4px 8px -4px rgb(0 0 0 / 10%), 0 8px 16px -8px rgb(0 0 0 / 12%);
    text-decoration: none;
    color: white;
    display: block;
    width: 100%;
    padding: 1rem;
    overflow: hidden;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
    border-radius: 10px;
    transition: transform 2s ease-in-out;
}

.btnGreen {
    background: linear-gradient(180deg, #11a158, #008d44);


}

.btnDark {
    background: linear-gradient(180deg, #323232, #000);

}

.btnLight {
    background: linear-gradient(180deg, rgb(250, 250, 250), rgb(230, 230, 230));
    color: #008d44;
    font-weight: 600;
    font-size: 11px;

}

.btnLight:hover {
    background: linear-gradient(180deg, rgb(250, 250, 250), rgb(230, 230, 230));
    color: #008d44;
}

.btns:hover {
    text-decoration: none;
    color: white;
    transform: scale(1.08);
}

.inputs {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem 3rem;
    border: 1px solid #008d44;
    background: none;
    border-radius: 5px;
}

.inputs:hover {
    border: 1px solid #008d44;
}

.paddings {
    padding: 1rem;
}