.styles_btns__I8Etp {
    border-radius: 10px;
    box-shadow: 0 1px 2px -1px rgb(0 0 0 / 2%), 0 2px 4px -2px rgb(0 0 0 / 8%), 0 4px 8px -4px rgb(0 0 0 / 10%), 0 8px 16px -8px rgb(0 0 0 / 12%);
